import React from 'react';
import { Button, Container, Typography } from '@mui/material';
import { styled } from '@mui/system';
import { Link } from 'react-router-dom';
const WelcomeMessage = styled(Typography)({
  marginBottom: '1.5rem',
});

const CardContainer = styled(Container)(({ theme }) => ({
  paddingTop: theme.spacing(8),
  paddingBottom: theme.spacing(8),
}));

const ImageContainer = styled('div')({
  display: 'flex',
  justifyContent: 'center',
  margin: '1.5rem',
  maxHeight:'500px'
});

const Home = () => {
  return (
    <Container maxWidth="md">
      <ImageContainer>
        <img src='/landmark.gif' alt="Landmark" style={{ maxWidth: '100%', height: 'auto' }} />
      </ImageContainer>
      <WelcomeMessage variant="h3" gutterBottom>Welcome to Our Aviator Game App</WelcomeMessage>
      <Typography variant="body1" paragraph>
        Experience the thrill of betting with us! We offer a wide range of exciting games and betting options to suit every player's preferences.
      </Typography>
      <Button LinkComponent={Link} to={'/betting'} fullWidth>
               Play Game Now
      </Button>
      <Typography variant="body1" paragraph>
        Whether you're a seasoned Bet or new to the world of online betting, our platform provides a safe and secure environment for you to enjoy your favorite games and win big.
      </Typography>
    </Container>
  );
}

export default Home;
