import React from 'react';
import { Typography,  Box} from '@mui/material';
import { FixedSizeList } from 'react-window';



const Transaction = ({ payment_id, amount, updated_at }) => {
    // Convert timestamp (assumed to be in seconds) to Date object
    const date = new Date(updated_at * 1000); // JavaScript Date works with milliseconds
  
    return (
      <Box
        display="flex"
        flexDirection="column"
        alignItems="stretch"
        padding={2}
        border="1px solid #ccc"
        borderRadius={1}
        marginBottom={2}
      >
        {

        typeof(payment_id) === 'string' ? (<Typography variant="subtitle1">Transaction ID:</Typography>) : (<Typography variant="subtitle1">BetX:</Typography>)
          }
        <Typography variant="body1">{payment_id}</Typography>
  
        <Box display="flex" alignItems="center" marginTop={1}>
          <Typography variant="subtitle1" marginRight={1}>
            Amount:
          </Typography>
          <Typography variant="body1">{typeof(payment_id) === 'string' ?   amount / 100 : amount}</Typography>
        </Box>
  
        <Box display="flex" alignItems="center" marginTop={1}>
          <Typography variant="subtitle1" marginRight={1}>
            Date:
          </Typography>
          <Typography variant="body1">{date.toLocaleString()}</Typography>
        </Box>
      </Box>
    );
  };
  


function TransactionList({ data }) {
  const renderRow = ({ index, style }) => (
    <div style={{ ...style, borderBottom: '1px solid #ccc', padding: '10px' }}>
      <Transaction {...data[index]} />
    </div>
  );

  return (
    <div>
      <FixedSizeList
        height={720}
        itemSize={175} // Adjusted itemSize for better visualization
        itemCount={data.length}
        overscanCount={5}
      >
        {renderRow}
      </FixedSizeList>
    </div>
  );
}

export default TransactionList;
