import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import { Link, redirect, useNavigate, useParams } from 'react-router-dom';
import {signin, authenticate, resetotp, getotp} from '../apis/user';
import LoaderOverlay from './loading';
import { userLoged } from '../apis/user';
import { getOnewidral, Updatewidral } from '../apis/bet';
export default function FormData() {
  const [isLoading, setLoading] = useState(false);
  
  let navigate = useNavigate()
  const userdata = userLoged()
  const {widral_id} = useParams()

  
  
  const [data, setdata] = useState({});
  const [paymentId, setpaymentId] = useState('')


  const handleChange = (event) => {
    setpaymentId(event.target.value);
  };

  const widralHandle = (e) => {
    e.preventDefault();
    console.log(widral_id)
  
    setLoading(true);
    Updatewidral(widral_id, {'payment_id': paymentId}).then((data) => {
      setLoading(false);
      if (!data) {
        return ;
      }
      if (data.error) {
        const a = data.error;
        return         }
      console.log(data)
      return navigate('/adminprofile', {replace : true})
    });
  };


  useEffect(()=>{
    if (!userdata && !userdata.access){
      navigate('/adminprofile')
    }
     getOnewidral(widral_id).then((data)=>{
      if (!data){
        return
      }
      if (data.status == 200){
        setdata(data.data)
      }
      else{
        console.log(data)
      }
     })     

  }, [])

  return (
    <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          padding: '50px',
          borderRadius: '20px',
          boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
          marginTop: '100px',
        }}
      >
        <Typography variant="h5" component="h1" gutterBottom margin={2}>
          WithDraw Money
        </Typography>
        {isLoading && <LoaderOverlay />}

         <Box component="form" sx={{ width: '100%' }}>
            <Grid container spacing={3}>
           { data && data.user && (<
            Grid item xs={12} margin={2}>
                <Typography>Name : {data.user.first_name}</Typography>
                <Typography>Email : {data.user.email} </Typography>
                <Typography>Amount : {data.amount} </Typography>
                <Typography>Currency : {data.currency} </Typography>
                </Grid>
              )}   
              </Grid>
              <Grid item xs={12}>
                <TextField
                  id="payment_id"
                  name="Payment ID"
                  label="Payment ID"
                  variant="outlined"
                  fullWidth
                  onChange={handleChange}
                  sx={{ mb: 2 }}
                />
              </Grid>
              <Grid container justifyContent="center" spacing={3} margin={1}>
              
              <Grid item>
                <Button variant="contained" color="primary" onClick={widralHandle}>
                  withDraw
                </Button>
                
               </Grid>
            
              
            </Grid>
          </Box>
          


      </Box>
    </Container>
  );
}
