import React , {useEffect} from 'react';
import { Route, Routes } from 'react-router-dom';
import './App.css';
import Home from './component/Home';
import SignUp from './component/Signup';
import Login from './component/Login';
import NavBar from './component/Navbar';
import Profile from './component/profile';
import { CssBaseline } from '@mui/material';
import Betting from './component/betking';
import NotFound from './component/404';
import Footer from './component/footer';
import AboutUs from './component/about';
import TermsAndConditions from './component/termsconditions';
import EditProfile from './component/editprofile';
import UpdatePassword from './component/changepassword';
import Checkout from './component/checkout';
import { themeOptions } from './component/themer';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import WidralMoney from './component/widrawal';
import AdminProfile from './component/adminprofile';
import CompleteWidral from './component/complete_widral';

function App() {

  useEffect(() => {
    document.title = "Air Daman Aviator";
  }, []);
  return (
    <>
    <ThemeProvider theme={createTheme(themeOptions)}>
    <CssBaseline /> 
   <div className='App-header'>
    <NavBar/>
    <Routes>
    <Route path='/' element = {<Home/>}/>
    <Route path='/signup' element = {<SignUp/>}/>
    <Route path='/login' element = {<Login/>}/>
    <Route path='/profile' element = {<Profile/>}/>
    <Route path='/betting' element = {<Betting/>}/>
    <Route path='/about-us' element = {<AboutUs/>}/>
    <Route path='/terms' element = {<TermsAndConditions/>}/>
    <Route path='/updateprofile' element = {<EditProfile/>}/>
    <Route path='/changepassword' element = {<UpdatePassword/>}/>
    <Route path='/checkout' element = {<Checkout/>}/>
    <Route path='/widral' element = {<WidralMoney/>}/>
    <Route path='/adminprofile' element = {<AdminProfile/>}/>
    <Route path='/widral/:widral_id' element = {<CompleteWidral/>}/>
    <Route path='*' element = {<NotFound/>}/>

    </Routes>
    <Footer/>
    </div>
    </ThemeProvider>
    </>
  );
}

export default App;
