import React, { useState , useEffect} from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import Avatar from '@mui/material/Avatar';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import { Link, redirect, useNavigate } from 'react-router-dom';
import { userUpdateApi, userProfile } from '../apis/user';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import countries from './conunty_code';
import LoaderOverlay from './loading';
import { Co2Sharp } from '@mui/icons-material';

export default function FormData() {
          const [isLoading, setLoading] = useState(false);
          let navigate = useNavigate()


          const [values, setValues] = useState({
            first_name: '',
            last_name: '',
            email: '',
            country_name: '',
            mob_num: '',
            city_name: '',
            gender: 0,
            redirect: false,
          });

          const [helperValues, setHelperValues] = useState({
            nameHelper: '',
            emailHelper: '',
            passwordHelper: '',
          });

          const { nameHelper, emailHelper, passwordHelper } = helperValues;

          const handleChange = (name) => (event) => {
            setValues({ ...values, [name]: event.target.value });
          };

          const errorHandler = (errObj) => {
            setHelperValues({ ...errObj });
          };

          const countryList = countries.map(
            (val) => '+ ' + val.phone + '  ' + val.label + '  ' + '(' + val.code + ')'
          );

          const updateHandle = (e) => {
            e.preventDefault();

            const {
              first_name,
              last_name,
              email,
              gender,
              country_name,
              city_name,
              mob_num,
            } = values;

            setLoading(true);
            userUpdateApi({
              first_name,
              last_name,
              email,
              country_name,
              city_name,
              mob_num: parseInt(mob_num),
              gender,
            }).then((data) => {
              setLoading(false);
              if (!data) {
              
                return ;
              }
              if (data.error) {
                const a = data.error;
                console.log(a)
                let errCurrentState = {};
                let adderrr = (name, err) => {
                  errCurrentState[name] = err;
                };
                let all_error = {}
                for (let key in a) {
                  let val = key + 'Helper'
                  console.log(val, a[key][0])
                  all_error[val] = a[key][0]
                  setHelperValues({...helperValues, val: a[key][0]});
                }
                setHelperValues({...all_error})
                
                }
                if (data.status == 200){
                    return navigate('/profile', {replace : true})  
                    
                }
                
              
            });
          };

          useEffect(()=>{
            userProfile().then((data)=>{
             if (data.status == 200){
               setValues({...values, ...data.data})
           
             }
            })
       }, [])
               

          return (
            <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  padding: '50px',
                  borderRadius: '20px',
                  boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
                  marginTop: '100px',
                }}
              >
                {isLoading && <LoaderOverlay />}

                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                  <AccountCircleIcon />
                </Avatar>
                <Typography variant="h5" component="h1" margin={2}>
                  Edit Profile
                </Typography>
                  <Box component="form" sx={{ width: '100%' }}>
                      <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            id="firstName"
                            name="firstname"
                            label="First Name"
                            variant="outlined"
                            fullWidth
                            value={values.first_name ? values.first_name : ''}
                            helperText= {nameHelper}
                            onChange={handleChange('first_name')}
                            sx={{ mb: 2 }}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            id="lastName"
                            name="lastname"
                            label="Last Name"
                            variant="outlined"
                            fullWidth
                            value={values.last_name ? values.last_name : ''}
                            helperText={helperValues.last_nameHelper}
                            onChange={handleChange('last_name')}
                            sx={{ mb: 2 }}
                          />
                        </Grid>

                        <Grid item xs={12}>
                          <TextField
                            id="email"
                            name="email"
                            label="Email"
                            variant="outlined"
                            fullWidth
                            value={values.email ? values.email : ''}
                            helperText = {emailHelper}
                            onChange={handleChange('email')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextField
                            id="mob_num"
                            name="mob_num"
                            label="Phone Number"
                            variant="outlined"
                            fullWidth
                            type="Number"
                            value={values.mob_num ? values.mob_num : ""}
                            onChange={handleChange('mob_num')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>
                        
                        <Grid item xs={12}>
                          <TextField
                            id="city"
                            name="city"
                            label="City Name"
                            variant="outlined"
                            type="text"
                            fullWidth
                            value={values.city_name ? values.city_name : ""}
                            onChange={handleChange('city_name')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>

                        <Grid item xs={12} sm={6}>
                          <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label1">
                              Gender
                            </InputLabel>
                            <Select
                              labelId="simple-select-label1"
                              id="simple-select1"
                              label="Gender"
                              defaultValue={3}
                              value={values.gender ? values.gender : 3}
                              onChange={handleChange('gender')}
                            >
                              {['Male', 'Female', 'Other', 'Select Gender'].map(
                                (val, index) => (
                                  <MenuItem value={index} key={index}>
                                    {val}
                                  </MenuItem>
                                )
                              )}
                            </Select>
                          </FormControl>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                          <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">
                              Country
                            </InputLabel>
                            <Select
                              labelId="simple-select-label"
                              id="simple-select"
                              label="Select Country"
                              defaultValue={'Select Counry'}
                              value={values.country_name ? values.country_name : 'Select Counry'}
                              onChange={handleChange('country_name')}
                            >
                              <MenuItem value={'Select Counry'} key={'country-1'}>
                                {'Select Country'}
                              </MenuItem>

                              {countryList.map((val, index) => (
                                <MenuItem value={val} key={index}>
                                  {val}
                                </MenuItem>
                              ))}
                            </Select>
                          </FormControl>
                        </Grid>

                        <Grid container justifyContent="center" spacing={3} margin={1}>
                          <Grid item>
                            <Button
                              variant="contained"
                              color="primary"
                              onClick={updateHandle}
                              disabled={isLoading}
                            >
                              Update
                            </Button>
                          </Grid>
                          <Grid
                            container
                            direction="column"
                            justifyContent="center"
                            alignItems="center"
                            margin={1}
                          >
                            <Grid justifyContent="center" item margin={1}>
                              <Link to="/changepassword" variant="body2">
                                {"Change Password"}
                              </Link>
                            </Grid>
                            <Grid justifyContent="center" item margin={1}>
                              <Link to="/profile" variant="body2">
                                {"Profile"}
                              </Link>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Box>
                  
              </Box>
            </Container>
          );
}
