import React from 'react';
import { Container, Typography, Grid } from '@mui/material';
import { styled } from '@mui/system';

const AboutUsContainer = styled(Container)(({ theme }) => ({
  paddingTop: theme.spacing(8),
  paddingBottom: theme.spacing(8),
}));

const AboutUs = () => {
  return (
    <AboutUsContainer maxWidth="md">
      <Typography variant="h3" gutterBottom>About Us</Typography>
      <Typography variant="body1" paragraph>
        Welcome to our gambling company! We pride ourselves on offering an unparalleled gambling experience that combines excitement, innovation, and integrity.
      </Typography>
      <Typography variant="h4" gutterBottom>Our Vision</Typography>
      <Typography variant="body1" paragraph>
        Our vision is to become the leading provider of online gambling entertainment, known for our commitment to fairness, transparency, and responsible gaming. We strive to continuously innovate and improve our offerings to ensure that our customers have the most thrilling and immersive experience possible.
      </Typography>
      <Typography variant="h4" gutterBottom>Address</Typography>
      <Typography variant="body1" paragraph>
        23, Main Street, New  Jersy, USA
      </Typography>
      <Typography variant="h4" gutterBottom>Contact Details</Typography>
      <Typography variant="body1" paragraph>
        Email: contact@airdamanaviator.com
      </Typography>
      <Typography variant="body1" paragraph>
        Phone: +1435-456-7890
      </Typography>
      <Typography variant="body1" paragraph>
        For any inquiries or assistance, feel free to reach out to us. We're here to help!
      </Typography>
    </AboutUsContainer>
  );
}

export default AboutUs;
