import React , {useState, useEffect} from 'react';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { styled } from '@mui/system';
import Grid from '@mui/material/Grid';
import { Paper } from '@mui/material';
import {userProfile} from '../apis/user';
import { emojis } from './dummydata';
import { Link,useNavigate } from 'react-router-dom';
import { userLoged } from '../apis/user';
import { getallwidral, getallCompletedwidral } from '../apis/bet';
import WidralListList from './widrallist';
// Styled components for the profile page elements
const Root = styled(Paper)({
  flexGrow: 1,
  padding: '2rem',
  margin: 'auto',
  minHeight: 600,
});

const Header = styled('div')({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  marginBottom: '2rem',
  backgroundColor:'inherit'
});

const AvatarWrapper = styled('div')(({ theme }) => ({
  width: theme.spacing(10),
  height: theme.spacing(10),
}));

const InfoSection = styled('div')({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'justfy-content',
  marginBottom: '1.5rem',
  
});


const InfoLabel = styled(Typography)({
  color: (props) => props.theme.palette.text.secondary,
  marginBottom: '0.5rem',
});

// Profile component with styled components
const AdminProfile = () => {

  const [userInfo, setuserInfo] = useState({})
  const [alltrans, setalltrans] = useState([])
  const [allwidral, setallwidral] = useState([])
  let navigate = useNavigate()
  const userdata = userLoged()

  const genderlist = ['Male', 'Female', 'Other']

  const buttonHandler = (id)=>{
      navigate(`/widral/${id}`)
  }
  
  useEffect(()=>{
      if (userdata && !userdata.access && !userdata.is_superuser){
        navigate('/login')
        return
       }
        
       userProfile().then((data)=>{
        if (!data){
          return
        }
        try{
          
        if (data.status === 200){
          setuserInfo({...userInfo, ...data.data})
      
        }
        // console.log(data)
        }catch(e){
          console.log(e)
        }
       })
       getallwidral().then((data)=>{
        if (!data){
          return
        }
        setallwidral(data)
        // console.log(data)
       })
       getallCompletedwidral().then((data)=>{
        if (!data){
          return
        }
        setalltrans(data)
        // console.log(data)
       })
       
  }, [])

  

  return (
    <Root>
      <Grid container spacing={2} justifyContent="center" alignItems="center">
        { userInfo.first_name &&(<>
        <Grid item xs={12} sm={6}>
          <Header>
            <AvatarWrapper>
              <Avatar alt="Emoji"  sx={{ width: 56, height: 56 }}>{emojis[Math.floor(Math.random() * 99)]}</Avatar>
            </AvatarWrapper>
            <Typography variant="h5">{userInfo.first_name + ' ' + userInfo.last_name}</Typography>
            <Typography variant="body2">{userInfo.city_name + ' ' + userInfo.country_name}</Typography>
          </Header>
              
          <InfoSection>
            <InfoLabel variant="body2">Email</InfoLabel>
            <Typography variant="body2">{userInfo.email}</Typography>
          </InfoSection>
          <InfoSection>
            <InfoLabel variant="body2">Phone</InfoLabel>
            <Typography variant="body2">{userInfo.mob_num}</Typography>
          </InfoSection>
          <InfoSection>
            <InfoLabel variant="body2">Gender</InfoLabel>
            <Typography variant="body2">{genderlist[userInfo.gender]}</Typography>
          </InfoSection>
          <InfoSection>
            <Link to='/updateprofile' variant="body2">
            <Button fullWidth variant='filled'>
              {"Edit Profile"}
              </Button>
                
                </Link> 
            <Link to='/changepassword' variant="body2">
            <Button fullWidth variant='filled'>
            
                  {"Change Password"}
                  
                </Button>
                </Link>
                
          </InfoSection>
          
        </Grid>

        </>)}
        <Grid item xs={12} sm={6}>
        <Grid container spacing={{ xs: 2, md: 3 }}>
               <Grid item xs={12} sm={6} md={6} >
                  <Typography variant='h5'>
                    All Requested WithDrawal
                  </Typography>
                  <WidralListList data = {allwidral} buttonHandler={buttonHandler} isbutton={true}/>
                 </Grid>
               <Grid item xs={12} sm={6} md={6} >
                  <Typography variant='h5'>
                    All Approved WithDrawal
                  </Typography>
                  <WidralListList data = {alltrans} isButton = {false}/>
                </Grid>
  
        </Grid> 
        
        </Grid>
      </Grid>
    </Root>
  );
};

export default AdminProfile;
