import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import Avatar from '@mui/material/Avatar';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { Link, redirect, useNavigate } from 'react-router-dom';
import {signin, authenticate, resetotp, getotp} from '../apis/user';
import LoaderOverlay from './loading';
import { userLoged } from '../apis/user';

export default function FormData() {
  const [isLoading, setLoading] = useState(false);
  const [otpsection, setOtpSection] = useState(false);
  const [otpbox, setoptbox] = useState(true)
  let navigate = useNavigate()
  const userdata = userLoged()

  
  
  const [values, setValues] = useState({
    email: '',
    password: '',
    otp_value:'',
    passwordHelper:'',
    otp_helper:'',
    confirm_password:""
  });

  const handleChange = (name) => (event) => {
    setValues({ ...values, passwordHelper:'',[name]: event.target.value });
  };

  const signinHandle = (e) => {
    e.preventDefault();

    const {
      email,
      password} = values;

    setLoading(true);
    signin({
      email,
      password}).then((data) => {
      setLoading(false);
      if (!data) {
      
        return ;
      }
      if (data.error) {
        const a = data.error;
        return setValues({...values, passwordHelper: a})
        }
      authenticate(data,()=>{
        console.log('Logged In')
      })  
      return navigate('/', {replace : true})
       
    });
  };

  const opthandler = (e)=>{
    e.preventDefault()
    
    const {email , otp_value, password, confirm_password} = values
    if (password != confirm_password){
      console.log('password not matched')
      return setValues({...values, passwordHelper: 'Password is not matched'})
    }
    
    if (!otp_value){
      console.log(values)
      console.log('OPT Unsuccessful')
      return ''
    }
    setLoading(true)
    resetotp({email, "otp": otp_value, password}).then((data)=>{
      setLoading(false)
      if (data.error){
        console.log(data)
        setValues({...values, passwordHelper: data.error})
      }
      // console.log(data)
      if (data.status === 200){
        
        setLoading(false)
        setOtpSection(false)
        setoptbox(true)
        setValues({
          email: '',
          password: '',
          otp_value:'',
          passwordHelper:'',
          otp_helper:'',
          confirm_password:""
        })
  
    
      }
    
    })

  }
  const sendopthandler = (e)=>{
    e.preventDefault()
    setLoading(true)
    getotp({"email": values.email}).then((data)=>{
      setLoading(false)
      if (data.error){
        console.log(data)
        setValues({...values, otp_helper: data})
      }
      setValues({...values, otp_value:''})
      console.log(data)
      setoptbox(false)
      

    })

  }


  useEffect(()=>{
    if (userdata && userdata.access){
      navigate('/profile')
   }
  }, [])

  return (
    <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          padding: '50px',
          borderRadius: '20px',
          boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
          marginTop: '100px',
        }}
      >
         <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
        <Typography variant="h5" component="h1" gutterBottom margin={2}>
          Sigin In
        </Typography>
        {isLoading && <LoaderOverlay />}

      { !otpsection ?


      (<>
        <Box component="form" sx={{ width: '100%' }}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                id="email"
                name="email"
                label="Email"
                variant="outlined"
                fullWidth
                onChange={handleChange('email')}
                sx={{ mb: 2 }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="password"
                name="password"
                label="Password"
                variant="outlined"
                type="password"
                fullWidth
                helperText = {values.passwordHelper}
                error = {Boolean(values.passwordHelper)}
                onChange={handleChange('password')}
                sx={{ mb: 3 }}
              />
            </Grid>
            <Grid container justifyContent="center" spacing={3} margin={1}>
              
              <Grid item>
                <Button variant="contained" color="primary" onClick={signinHandle}>
                  SignIn
                </Button>
                
              </Grid>
              <Grid
                container
                direction="column"
                justifyContent="center"
                alignItems="center"
                 margin={1}>
                  <Grid justifyContent='center' item margin={1}>
                  <Link href="#" variant="body2" onClick={(e)=> setOtpSection(true)}>
                  {"Forget password"}
                </Link> 
                  </Grid>
                  <Grid justifyContent='center' item margin={1}>
                  <Link to='/signup' variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link> 
                  </Grid>

                 </Grid>
            </Grid>
          </Grid>
        </Box>
       </>):
        !otpbox ?
        (<>
        <Box component="form" sx={{ width: '100%' }}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                id="Opt1"
                name="otp1"
                label="OTP: One time password"
                variant="outlined"
                fullWidth
                autoComplete={false}
                helperText  = {values.otp_helper}
                onChange={handleChange('otp_value')}
                sx={{ mb: 3 }}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                id="new_password"
                name="new_password"
                label="New Password"
                variant="outlined"
                type="password"
                fullWidth
                onChange={handleChange('password')}
                sx={{ mb: 3 }}
              />
            </Grid>
            
            <Grid item xs={12}>
              <TextField
                id="conf_password"
                name="conf_password"
                label="Re-enter Password"
                variant="outlined"
                type="password"
                fullWidth
                error ={Boolean(values.passwordHelper)}
                helperText = {values.passwordHelper}
                onChange={handleChange('confirm_password')}
                sx={{ mb: 3 }}
              />
            </Grid>
            
            <Grid container justifyContent="center" spacing={3} margin={1}>
              
              <Grid item>
                <Button variant="contained" color="primary" onClick={opthandler}>
                  Verify
                </Button>
                
              </Grid>
              <Grid
                container
                direction="column"
                justifyContent="center"
                alignItems="center"
                 margin={1}>
                  <Grid justifyContent='center' item margin={1}>
                  <Link href="#" variant="body2" onClick={(e)=> {
                    setOtpSection(false)
                    setoptbox(true)
                  }}>
                  {"Sign In"}
                </Link> 
                  </Grid>
                  <Grid justifyContent='center' item margin={1}>
                  <Link to='/signup' variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link> 
                  </Grid>

                 </Grid>
            </Grid>
          </Grid>
        </Box>
        </>)
        :
        (<>
          <Box component="form" sx={{ width: '100%' }}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <TextField
                  id="email"
                  name="email"
                  label="Email"
                  variant="outlined"
                  fullWidth
                  onChange={handleChange('email')}
                  sx={{ mb: 2 }}
                />
              </Grid>
              <Grid container justifyContent="center" spacing={3} margin={1}>
                
                <Grid item>
                  <Button variant="contained" color="primary" onClick={sendopthandler}>
                    Send Otp
                  </Button>
                  
                </Grid>
                <Grid
                  container
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                   margin={1}>
                    <Grid justifyContent='center' item margin={1}>
                    <Link href="#" variant="body2" onClick={(e)=> {
                      setOtpSection(false)
                      setoptbox(false)

                    }}>
                    {"Sign In"}
                  </Link> 
                    </Grid>
                    <Grid justifyContent='center' item margin={1}>
                    <Link to='/signup' variant="body2">
                    {"Don't have an account? Sign Up"}
                  </Link> 
                    </Grid>
  
                   </Grid>
              </Grid>
            </Grid>
          </Box>
          </>)
          



        }








      </Box>
    </Container>
  );
}
