import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import Avatar from '@mui/material/Avatar';
import { Link,  useNavigate } from 'react-router-dom';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import {transferMoney} from '../apis/bet';
import LoaderOverlay from './loading';

export default function FormData() {
  const [isLoading, setLoading] = useState(false);
  let navigate = useNavigate()

  const [values, setValues] = useState({
    amount: '',
    currency:'INR',
    address:'',
    error:''
  });

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const paymentHandle = (e) => {
    e.preventDefault();
    const {amount, currency} = values
    setLoading(true);
    
    transferMoney({amount, currency}).then((data)=>{
      if (data.error){
        console.log(data)
        setLoading(false)
        setValues({...values, error: data.error})
      }
      if (data.status === 200){
        return navigate('/profile')
        
      }

    })

    
  };

  


  return (
    <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          padding: '50px',
          borderRadius: '20px',
          boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
          marginTop: '100px',
        }}
      >
         <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <AccountBalanceIcon />
          </Avatar>
        <Typography variant="h5" component="h1" gutterBottom margin={2}>
          Widral Money
        </Typography>
        {isLoading && <LoaderOverlay />}

        <Box component="form" sx={{ width: '100%' }}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <TextField
                id="amount"
                name="amount"
                label="Amount"
                variant="outlined"
                fullWidth
                type='Number'
                onChange={handleChange('amount')}
                sx={{ mb: 2 }}
              />
            </Grid>

               <Grid item xs={12} sm={6}>
                          <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label1">
                              Currency
                            </InputLabel>
                            <Select
                              labelId="simple-select-label1"
                              id="simple-select1"
                              label="Currency"
                              defaultValue={'INR'}
                              onChange={handleChange('gender')}
                            >
                              {['INR', 'USD', 'EUR'].map(
                                (val, index) => (
                                  <MenuItem value={val} key={index}>
                                    {val}
                                  </MenuItem>
                                )
                              )}
                            </Select>
                          </FormControl>
                        </Grid>

            
            <Grid container justifyContent="center" spacing={3} margin={1}>
              
              <Grid item>
                <Button variant="contained" color="primary" onClick={paymentHandle}>
                  Widral
                </Button>
                
              </Grid>
              <Grid
                container
                direction="column"
                justifyContent="center"
                alignItems="center"
                 margin={1}>
                  <Grid justifyContent='center' item margin={1}>
                  <Link to='/profile' variant="body2">
                  {"Profile"}
                </Link> 
                  </Grid>

                 </Grid>
            </Grid>
          </Grid>
        </Box>
       

      </Box>
    </Container>
  );
}
