import React from 'react';
import { Container, Typography } from '@mui/material';
import { styled } from '@mui/system';


const TemrsContainer = styled(Container)(({ theme }) => ({
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  }));
  


const TermsAndConditions = () => {
  return (
    <TemrsContainer maxWidth="md">
      <Typography variant="h3" gutterBottom>Terms and Conditions</Typography>
      <Typography variant="body1" paragraph>
        Welcome to our gambling platform! These Terms and Conditions outline the rules and regulations for the use of our website.
      </Typography>
      <Typography variant="h4" gutterBottom>1. Acceptance of Terms</Typography>
      <Typography variant="body1" paragraph>
        By accessing this website, you agree to be bound by these Terms and Conditions, all applicable laws, and regulations, and agree that you are responsible for compliance with any applicable local laws.
      </Typography>
      <Typography variant="h4" gutterBottom>2. Age Restriction</Typography>
      <Typography variant="body1" paragraph>
        You must be at least 18 years old to use our services. By using our services, you warrant and represent that you are at least 18 years of age and legally capable of entering into binding contracts.
      </Typography>
      <Typography variant="h4" gutterBottom>3. Responsible Gambling</Typography>
      <Typography variant="body1" paragraph>
        We promote responsible gambling and urge our users to gamble responsibly. Gambling should be treated as a form of entertainment, and you should only gamble with money that you can afford to lose.
      </Typography>
      <Typography variant="h4" gutterBottom>4. Account Registration</Typography>
      <Typography variant="body1" paragraph>
        In order to access certain features of our platform, you may be required to create an account. You are responsible for maintaining the confidentiality of your account and password and for restricting access to your computer.
      </Typography>
      <Typography variant="h4" gutterBottom>5. Fair Play</Typography>
      <Typography variant="body1" paragraph>
        We are committed to ensuring fair play on our platform. Our games are tested and certified to ensure fairness and randomness. Any form of cheating or collusion will result in immediate termination of your account.
      </Typography>
      <Typography variant="h4" gutterBottom>6. Disclaimer of Warranty</Typography>
      <Typography variant="body1" paragraph>
        The information contained on our website is provided on an "as is" basis. We make no warranties, expressed or implied, and hereby disclaim and negate all other warranties including, without limitation, implied warranties or conditions of merchantability.
      </Typography>
      <Typography variant="body1" paragraph>
        For the full terms and conditions, please refer to the detailed document provided on our website.
      </Typography>
    </TemrsContainer>
  );
}

export default TermsAndConditions;
