import React, { useState , useEffect} from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import Avatar from '@mui/material/Avatar';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { Link, useNavigate } from 'react-router-dom';
import { signup, opt } from '../apis/user';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import countries from './conunty_code';
import LoaderOverlay from './loading';
import { userLoged } from '../apis/user';


export default function FormData() {
          const [isLoading, setLoading] = useState(false);
          const [otpsection, setOtpSection] = useState(false);
          let navigate = useNavigate()
          const userdata = userLoged()
        

          const [values, setValues] = useState({
            first_name: '',
            last_name: '',
            email: '',
            password: '',
            confirm_password: '',
            country_name: '',
            mob_num: '',
            city_name: '',
            gender: 0,
            otp_value:'',
            redirect: false,
          });

          const [helperValues, setHelperValues] = useState({
            nameHelper: '',
            emailHelper: '',
            passwordHelper: '',
          });

          const { nameHelper, emailHelper, passwordHelper } = helperValues;

          const handleChange = (name) => (event) => {
            setValues({ ...values, [name]: event.target.value });
            setHelperValues({ nameHelper: '',
              emailHelper: '',
              passwordHelper: '',
            })
          };

          const errorHandler = (errObj) => {
            setHelperValues({ ...errObj });
          };

          const countryList = countries.map(
            (val) => '+ ' + val.phone + '  ' + val.label + '  ' + '(' + val.code + ')'
          );

          const signHandle = (e) => {
            e.preventDefault();

            const {
              first_name,
              last_name,
              email,
              password,
              confirm_password,
              gender,
              country_name,
              city_name,
              mob_num,
            } = values;
 


            if (password !== confirm_password) {
              return setHelperValues({ passwordHelper: 'Passwords do not match' });
            }
            setLoading(true);
            signup({
              first_name,
              last_name,
              email,
              password,
              country_name,
              city_name,
              mob_num: parseInt(mob_num),
              gender,
            }).then((data) => {
              setLoading(false);
              if (!data) {
              
                return ;
              }
              if (data.error) {
                const a = data.error;
                // console.log(a)
                let errCurrentState = {};
                let adderrr = (name, err) => {
                  errCurrentState[name] = err;
                };
                let all_error = {}
                for (let key in a) {
                  let val = key + 'Helper'
                  // console.log(val, a[key][0])
                  all_error[val] = a[key][0]
                  setHelperValues({...helperValues, val: a[key][0]});
                }
                setHelperValues({...all_error})

                }
              if (data.status === 201 || data.status === 200) {
                setOtpSection(true);
              }
            });
          };


          const opthandler = (e)=>{
            e.preventDefault()
            
            const {email , otp_value} = values
            
            if (!otp_value){
              console.log('OPT Unsuccessful')
              return ''
            }
            setLoading(true)
            opt({email, "otp": otp_value}).then((data)=>{
              setLoading(false)
              
              console.log(data)

              return navigate('/login', {replace : true})
            })

          }
          


          useEffect(()=>{
            if (userdata && userdata.access){
              navigate('/profile')
           }
          }, [])

          return (
            <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  padding: '50px',
                  borderRadius: '20px',
                  boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
                  marginTop: '100px',
                }}
              >
                {isLoading && <LoaderOverlay />}

                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                  <LockOutlinedIcon />
                </Avatar>
                <Typography variant="h5" component="h1" margin={2}>
                  Sign Up
                </Typography>

                {!otpsection ? (
                  <>
                    <Box component="form" sx={{ width: '100%' }}>
                      <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            id="firstName"
                            name="firstname"
                            label="First Name"
                            variant="outlined"
                            fullWidth
                            helperText= {nameHelper}
                            onChange={handleChange('first_name')}
                            sx={{ mb: 2 }}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <TextField
                            id="lastName"
                            name="lastname"
                            label="Last Name"
                            variant="outlined"
                            fullWidth
                            onChange={handleChange('last_name')}
                            sx={{ mb: 2 }}
                          />
                        </Grid>

                        <Grid item xs={12}>
                          <TextField
                            id="email"
                            name="email"
                            label="Email"
                            variant="outlined"
                            fullWidth
                            helperText = {emailHelper}
                            onChange={handleChange('email')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextField
                            id="mob_num"
                            name="mob_num"
                            label="Phone Number"
                            variant="outlined"
                            fullWidth
                            type="Number"
                            onChange={handleChange('mob_num')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextField
                            id="password"
                            name="password"
                            label="Password"
                            variant="outlined"
                            type="password"
                            fullWidth
                            helperText = {passwordHelper}
                            error = {Boolean(passwordHelper)}
                            onChange={handleChange('password')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextField
                            id="repassword"
                            name="repassword"
                            label="Re-enter Password"
                            variant="outlined"
                            type="password"
                            helperText = {passwordHelper}
                            error = {Boolean(passwordHelper)}
                            onChange={handleChange('confirm_password')}
                            fullWidth
                            sx={{ mb: 3 }}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextField
                            id="city"
                            name="city"
                            label="City Name"
                            variant="outlined"
                            type="text"
                            fullWidth
                            onChange={handleChange('city_name')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>

                        <Grid item xs={12} sm={6}>
                          <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label1">
                              Gender
                            </InputLabel>
                            <Select
                              labelId="simple-select-label1"
                              id="simple-select1"
                              label="Gender"
                              defaultValue={3}
                              onChange={handleChange('gender')}
                            >
                              {['Male', 'Female', 'Other', 'Select Gender'].map(
                                (val, index) => (
                                  <MenuItem value={index} key={index}>
                                    {val}
                                  </MenuItem>
                                )
                              )}
                            </Select>
                          </FormControl>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                          <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">
                              Country
                            </InputLabel>
                            <Select
                              labelId="simple-select-label"
                              id="simple-select"
                              label="Select Country"
                              defaultValue={'Select Counry'}
                              onChange={handleChange('country_name')}
                            >
                              <MenuItem value={'Select Counry'} key={'country-1'}>
                                {'Select Country'}
                              </MenuItem>

                              {countryList.map((val, index) => (
                                <MenuItem value={val} key={index}>
                                  {val}
                                </MenuItem>
                              ))}
                            </Select>
                          </FormControl>
                        </Grid>

                        <Grid container justifyContent="center" spacing={3} margin={1}>
                          <Grid item>
                            <Button
                              variant="contained"
                              color="primary"
                              onClick={signHandle}
                              disabled={isLoading}
                            >
                              SignIn
                            </Button>
                          </Grid>
                          <Grid
                            container
                            direction="column"
                            justifyContent="center"
                            alignItems="center"
                            margin={1}
                          >
                            <Grid justifyContent="center" item margin={1}>
                              <Link to="/login" variant="body2">
                                {"You have an account?  Login"}
                              </Link>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Box>
                  </>
                ) : (
                  <>
                    <Box sx={{ width: '100%' }}>
                      <Grid container spacing={3}>
                        <Grid item xs={12}>
                          <TextField
                            id="opt"
                            name="otp"
                            label="OTP: one time password"
                            variant="outlined"
                            fullWidth
                            type="text"
                            onChange={handleChange('otp_value')}
                            sx={{ mb: 3 }}
                          />
                        </Grid>

                        <Grid
                          container
                          justifyContent="center"
                          spacing={3}
                          margin={1}
                        >
                          <Grid item>
                            <Button
                              variant="contained"
                              color="primary"
                              onClick={opthandler}
                              disabled={isLoading}
                            >
                              Otp
                            </Button>
                          </Grid>
                          <Grid
                            container
                            direction="column"
                            justifyContent="center"
                            alignItems="center"
                            margin={1}
                          >
                            <Grid justifyContent="center" item margin={1}>
                              <Link to="/login" variant="body2">
                                {"You have an account?  Login"}
                              </Link>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Box>
                  </>
                )}
              </Box>
            </Container>
          );
}
