import React, { useRef, useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import { styled } from '@mui/system';
import { Typography } from '@mui/material';

const VideoContainer = styled('div')({
  position: 'relative',
  width: '100%',
  paddingTop: '10.25%', // 16:9 Aspect Ratio (divide 9 by 16 = 0.5625)
  borderRadius: '16px', // Adding rounded corners (reduced from 100px for a more subtle effect)
});

const CenteredText = styled('div')({
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  textAlign: 'center',
  color: 'white',
  backgroundColor:'black'
});

const VideoPlayer = ({ videoUrl, jumpCondition, displayText, isMutedProp }) => {
  const playerRef = useRef(null);
  const [isSeeking, setIsSeeking] = useState(false);
  const [playbackStarted, setPlaybackStarted] = useState(false);
  const [isMuted, setIsMuted] = useState(isMutedProp); // Initialize isMuted with prop value

  useEffect(() => {
    const jumpToTime = (jumptime) => {
      if (!playerRef.current || !jumptime) return;
      setIsSeeking(true);
      playerRef.current.seekTo(jumptime, 'seconds');
    };

    if (jumpCondition) {
      jumpToTime(jumpCondition); // Jump to specified time based on jumpCondition prop
    }
  }, [jumpCondition]);

  const handleSeekComplete = () => {
    setIsSeeking(false);
    if (playerRef.current && !playbackStarted) {
      const internalPlayer = playerRef.current.getInternalPlayer();
      if (internalPlayer) {
        internalPlayer.play().then(() => {
          setPlaybackStarted(true);
        }).catch(error => {
          console.error('Failed to start playback:', error);
        });
      }
    }
  };

  useEffect(() => {
    setIsMuted(isMutedProp); // Update isMuted state when isMutedProp changes
  }, [isMutedProp]);

  return (
    <VideoContainer>
      <ReactPlayer
        ref={playerRef}
        url={videoUrl}
        playing={!isSeeking}
        muted={isMuted}
        controls={false}
        width="100%"
        height="100%"
        onSeek={handleSeekComplete}
        config={{
          file: {
            attributes: {
              crossOrigin: 'anonymous',
            },
          },
        }}
      />
      {displayText && (
        <CenteredText>
          <Typography variant="h4" component="div">
            {displayText.toFixed(2)}X  Money
          </Typography>
        </CenteredText>
      )}
    </VideoContainer>
  );
};

export default VideoPlayer;
