import {dummyusers, emojis} from './dummydata';

function getRandomSubset(array, size) {
    // Shuffle array
    let shuffled = array.slice();
    for (let i = shuffled.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
    }
    
    // Return subset
    return shuffled.slice(0, size);
}
    
function generateRandomValues(min, max, count) {
    let randomValues = [];
    for (let i = 0; i < count; i++) {
        let randomValue = Math.floor(Math.random() * (max - min + 1)) + min;
        randomValues.push(randomValue);
    }
    return randomValues;
}
    
function generateRandomNumber() {
    // Generate a random length between 0 and 6
    let length = Math.floor(Math.random() * 7); // 0 to 6 inclusive

    // Generate random digits based on the length
    let randomNumber = '';
    for (let i = 0; i < length; i++) {
        let digit = Math.floor(Math.random() * 10); // 0 to 9
        randomNumber += digit;
    }

    // Convert the result to a number (if you want)
    // Otherwise, randomNumber can be kept as a string if needed.
    return Number(randomNumber);
}

export const  get100users = ()=>{
    const user100 = getRandomSubset(dummyusers, 100)
    const emo100 = generateRandomValues(0,99,100)
    let userdata = []
    for(let i=0; i < user100.length; i++){
        userdata.push({
            username:user100[i][0]+ ' ' + user100[i][1] ,
            value: generateRandomNumber(),
            avatarUrl: emojis[emo100[i]],
        })
    }
    return userdata

}    