import * as React from 'react';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';
import PersonAdd from '@mui/icons-material/PersonAdd';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import LogoutIcon from '@mui/icons-material/Logout';
import LoginIcon from '@mui/icons-material/Login';
import PersonAddAltIcon from '@mui/icons-material/PersonAddAlt';
import { Link } from 'react-router-dom';
import { userLoged, logout } from '../apis/user';
import { useNavigate } from 'react-router-dom';
import { styled } from '@mui/system';
const LogoImage = styled('img')({
  maxHeight: 80, // Adjust size as necessary
  margin: '8px', // Optional: Adjust spacing
});




export default function AccountMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const logedInfo = userLoged();
  const open = Boolean(anchorEl);
  let navigate = useNavigate()
          

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between', // Center horizontally
        textAlign: 'center',
        boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
        minHeight: '80px',
      }}
    >
      <LogoImage src="/logo.png" />
      <List sx={{ display: 'flex' }}>
        <ListItemButton component={Link} to="/">
          <ListItemText>
            <Typography variant="h6">Home</Typography>
          </ListItemText>
        </ListItemButton>
        <ListItemButton component={Link} to="/betting">
          <ListItemText>
            <Typography variant="h6">Betting</Typography>
          </ListItemText>
        </ListItemButton>
      </List>

      <Tooltip title="Account settings">
        <IconButton
          onClick={handleClick}
          size="small"
          sx={{ ml: 2 }}
          aria-controls={open ? 'account-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
        >
          <Avatar sx={{ width: 32, height: 32, m: 1, bgcolor: 'secondary.main' }}>
            <AccountBoxIcon />
          </Avatar>
        </IconButton>
      </Tooltip>

      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            overflow: 'visible',
            filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
            mt: 1.5,
            '& .MuiAvatar-root': {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            '&::before': {
              content: '""',
              display: 'block',
              position: 'absolute',
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: 'background.paper',
              transform: 'translateY(-50%) rotate(45deg)',
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
      >
        {logedInfo.access ? (
          [
            <MenuItem key="profile" onClick={handleClose} component={Link} to="/profile">
              <ListItemIcon>
                <PersonAdd fontSize="small" />
              </ListItemIcon>
              Profile
            </MenuItem>,
            <MenuItem key="addMoney" onClick={handleClose} component={Link} to="/checkout">
              <ListItemIcon>
                <AccountBalanceWalletIcon fontSize="small" />
              </ListItemIcon>
              Add money
            </MenuItem>,
            <MenuItem key="logout" onClick={(e) => {
              e.preventDefault();
              logout();
              handleClose(e);
              navigate('/')
            }}>
              <ListItemIcon>
                <LogoutIcon fontSize="small" />
              </ListItemIcon>
              Logout
            </MenuItem>
          ]
        ) : (
          [
            <MenuItem key="login" onClick={handleClose} component={Link} to="/login">
              <ListItemIcon>
                <LoginIcon fontSize="small" />
              </ListItemIcon>
              Login
            </MenuItem>,
            <MenuItem key="signup" onClick={handleClose} component={Link} to="/signup">
              <ListItemIcon>
                <PersonAddAltIcon fontSize="small" />
              </ListItemIcon>
              SignUp
            </MenuItem>
          ]
        )}
      </Menu>
    </Box>
  );
}
