import { createTheme } from '@mui/material/styles';

// Define your custom theme options
export const themeOptions = createTheme({
  palette: {
    mode: 'dark', // Set dark mode
    primary: {
      main: '#90caf9', // Adjust primary color
    },
    secondary: {
      main: '#f48fb1', // Adjust secondary color
    },
    background: {
      default: '#121212', // Dark background color
      paper: '#282c34', // Dark surface color
    },
    text: {
      primary: '#ffffff', // White text color
      secondary: '#bdbdbd', // Light grey text color
    },
    action: {
      active: '#ffffff', // Adjust link (active) color
    },
  },
  typography: {
    fontFamily: 'Roboto, Arial, sans-serif', // Specify font family
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none', // Prevent uppercase text in buttons
        },
      },
    },
    MuiLink: {
      styleOverrides: {
        root: {
          textDecoration: 'underline', // Underline links
        },
      },
    },
    // Add overrides for other components as needed
  },
});

export default themeOptions;
