import React from 'react';
import { Container, Grid, Typography, Button } from '@mui/material';
import ErrorIcon from '@mui/icons-material/Error';
import { useNavigate } from 'react-router-dom'; // For navigation

const NotFound = () => {
  const navigate = useNavigate();

  return (
    <Container maxWidth="lg" sx={{ minHeight: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6} sx={{ marginBottom: 4 }}>
          <ErrorIcon fontSize="large" color="error" />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="h3">Oh no! Page not found.</Typography>
          <Typography variant="h5">The page you're looking for doesn't seem to exist.</Typography>
          <Button variant="contained" sx={{ marginTop: 4 }} onClick={() => navigate('/')}>
            Go back home
          </Button>
        </Grid>
      </Grid>
    </Container>
  );
};

export default NotFound;
