import React, { useState} from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import Avatar from '@mui/material/Avatar';
import { Link, useNavigate } from 'react-router-dom';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import {transaction, updatetransaction} from '../apis/bet';
import useRazorpay from "react-razorpay";
import { userLoged } from '../apis/user';
import LoaderOverlay from './loading';
import { RoZar_Customer_ID } from '../apis/config';


export default function FormData() {
  const [isLoading, setLoading] = useState(false);
  let navigate = useNavigate();
  const [Razorpay] = useRazorpay();

  const [values, setValues] = useState({
    amount: '',
    currency:'INR',
    address:'',
    error:''
  });

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const paymentHandle = (e) => {
    e.preventDefault();
    const {amount, currency} = values
    setLoading(true);
    
    transaction({amount, currency}).then((data)=>{
      if (data.error){
        console.log(data)
        setLoading(false)
        setValues({...values, error: data.error})
      }
      if (data.status === 200){
        const {amount, currency, id}  = data.data
        const {email, name} = userLoged()
        const options = {
          key: RoZar_Customer_ID, // Enter the Key ID generated from the Dashboard
          amount: amount, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
          currency: currency,
          name: "AirDaman Aviator",
          description: "Add Money to your Wallet",
          order_id: id, //This is a sample Order ID. Pass the `id` obtained in the response of createOrder().
          handler: function (response) {
            let update_data = {
              "order_id": response.razorpay_order_id,
              "payment_id":response.razorpay_payment_id,
              "signature":response.razorpay_signature
               }
            //  console.log(update_data)
             updatetransaction(update_data).then((data)=>{
              // console.log(data)
              setLoading(false)
              if (data.status === 200){
                return navigate('/profile', {replace : true})
              }
            })
            

          },
          prefill: {
            name: name,
            email:email,
            
          },
          notes: {
            address: values.address,
          },
          theme: {
            color: "#3399cc",
          },
        };
      
        const rzp1 = new Razorpay(options);
      
        rzp1.on("payment.failed", function (response) {
          alert(response.error.code);
          alert(response.error.description);
          alert(response.error.source);
          alert(response.error.step);
          alert(response.error.reason);
          alert(response.error.metadata.order_id);
          alert(response.error.metadata.payment_id);
        });
      
        rzp1.open();


      }

    })

    
  };

  


  return (
    <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          padding: '50px',
          borderRadius: '20px',
          boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
          marginTop: '100px',
        }}
      >
         <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <AccountBalanceIcon />
          </Avatar>
        <Typography variant="h5" component="h1" gutterBottom margin={2}>
          Add Money
        </Typography>
        {isLoading && <LoaderOverlay />}

        <Box component="form" sx={{ width: '100%' }}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <TextField
                id="amount"
                name="amount"
                label="Amount"
                variant="outlined"
                fullWidth
                type='Number'
                onChange={handleChange('amount')}
                sx={{ mb: 2 }}
              />
            </Grid>

               <Grid item xs={12} sm={6}>
                          <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label1">
                              Currency
                            </InputLabel>
                            <Select
                              labelId="simple-select-label1"
                              id="simple-select1"
                              label="Currency"
                              defaultValue={'INR'}
                              onChange={handleChange('gender')}
                            >
                              {['INR', 'USD', 'EUR'].map(
                                (val, index) => (
                                  <MenuItem value={val} key={index}>
                                    {val}
                                  </MenuItem>
                                )
                              )}
                            </Select>
                          </FormControl>
                        </Grid>

            
            <Grid item xs={12}>
              <TextField
                id="address"
                name="address"
                label="Address"
                variant="outlined"
                fullWidth
                onChange={handleChange('confirm_password')}
                sx={{ mb: 3 }}
              />
            </Grid>
            <Grid container justifyContent="center" spacing={3} margin={1}>
              
              <Grid item>
                <Button variant="contained" color="primary" onClick={paymentHandle}>
                  Pay
                </Button>
                
              </Grid>
              <Grid
                container
                direction="column"
                justifyContent="center"
                alignItems="center"
                 margin={1}>
                  <Grid justifyContent='center' item margin={1}>
                  <Link to='/profile' variant="body2">
                  {"Profile"}
                </Link> 
                  </Grid>

                 </Grid>
            </Grid>
          </Grid>
        </Box>
       

      </Box>
    </Container>
  );
}
