import React from 'react';
import { Typography, Paper, Grid , Box, Button} from '@mui/material';
import { styled } from '@mui/material/styles';
import { FixedSizeList } from 'react-window';


// Define the styled Paper component
const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(2),
  marginBottom: theme.spacing(2),
  width: '100%', // Ensures the Paper takes full width of its container
}));

const Transaction = ({id, user, amount, currency, buttonHandler, isbutton }) => {
    
    return (
      <Box
        display="flex"
        flexDirection="column"
        alignItems="stretch"
        padding={2}
        border="1px solid #ccc"
        borderRadius={1}
        marginBottom={2}
      >
     <Typography variant="subtitle1">User ID :</Typography>
          
        <Typography variant="body1">{user}</Typography>
  
        <Box display="flex" alignItems="center" marginTop={1}>
          <Typography variant="subtitle1" marginRight={1}>
            Amount :
          </Typography>
          <Typography variant="body1">{amount}</Typography>
        </Box>
  
        <Box display="flex" alignItems="center" marginTop={1}>
          <Typography variant="subtitle1" marginRight={1}>
            Currency :
          </Typography>
          <Typography variant="body1">{currency}</Typography>
        </Box>

        { isbutton && (<Button variant="contained" color="primary" onClick={(e)=> buttonHandler(id)}>
                  withDraw
                </Button>)}
        </Box>
    );
  };
  


function WidralList({ data , buttonHandler, isbutton}) {
  const renderRow = ({ index, style }) => (
    <div style={{ ...style, borderBottom: '1px solid #ccc', padding: '10px' }}>
      <Transaction {...data[index]} buttonHandler={buttonHandler} isbutton = {isbutton}/>
    </div>
  );

  return (
    <div>
      <FixedSizeList
        height={720}
        itemSize={250} // Adjusted itemSize for better visualization
        itemCount={data.length}
        overscanCount={5}
      >
        {renderRow}
      </FixedSizeList>
    </div>
  );
}

export default WidralList;
