import React, { useState, useEffect, useRef } from 'react';
import { Grid, Paper, Typography, TextField, Button, Box} from '@mui/material';
import MyAlert from './alert';
import LoaderOverlay from './loading';
import { useTheme } from '@mui/material/styles';
import ListComponent from './listcomponent';
import { getBalance, addBettingMoney } from '../apis/bet';
import VideoPlayer from './vid'; // Correct import statement
import { generateRandomNumberWithProbability} from './genradomnum';
import { get100users } from './userlisthelper';
const AddMoney = ({ addMoney, amount, makebet, handlebet, balance, betrequest, betXdata, setbalance }) => {
  
  const [isLoading, setLoading] = useState(false);

  const handleChange = (e) => {
    addMoney(e.target.value - amount);
  };

  const buttonHandleChange = (val) => {
    addMoney(val);
  };

  useEffect(()=>{
    const submitBet = () => {
      // console.log(makebet, amount, balance)
      if (makebet && amount && amount <= balance) {
        setLoading(true)
        addBettingMoney({ amount, betx: betXdata.toFixed(2) }).then((data) => {
          setLoading(false)
          if (data.status === 200){
            setbalance((prev)=>(prev - amount) + amount * betXdata.toFixed(2))
         
          }
          // console.log(data); // Handle success or error
        }).catch((error) => {
          console.error('Error placing bet:', error);
          // Handle error if needed
        });
      }
        
      // console.log('No amount added')  
      
    };
    submitBet()
  }, [betrequest])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: '50px',
        borderRadius: '20px',
        boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
        marginTop: '100px',
      }}
    >
      {isLoading && <LoaderOverlay />}

      <Box component="form" sx={{ width: '100%' }}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={12} justifyContent="center">
            <Typography>Total Balance : {balance.toFixed(2)}</Typography>
          </Grid>

          <Grid item xs={12} sm={12} justifyContent="center">
            <TextField
              id="amount"
              name="amount"
              label="Amount"
              variant="outlined"
              fullWidth
              type="Number"
              sx={{ mb: 2 }}
              value={amount}
              onChange={handleChange}
            />
          </Grid>

          <Grid container justifyContent="center" spacing={3} margin={1}>
            <Grid item xs={4} sm={2}>
              <Button variant="contained" color="primary" onClick={(e) => buttonHandleChange(-100)}>
                100-
              </Button>
            </Grid>

            <Grid item xs={4} sm={4}>
              <Button variant="contained" color="primary" fullWidth onClick={handlebet} disabled={makebet}>
                BET
              </Button>
            </Grid>

            <Grid item xs={4} sm={2}>
              <Button variant="contained" color="primary" onClick={(e) => buttonHandleChange(100)}>
                100+
              </Button>
            </Grid>
          </Grid>

          <Grid container justifyContent="center" spacing={3} margin={1}>
            <Grid item xs={4} sm={2}>
              <Button variant="contained" color="primary" onClick={(e) => buttonHandleChange(500)}>
                +500
              </Button>
            </Grid>
            <Grid item xs={4} sm={2}>
              <Button variant="contained" color="primary" onClick={(e) => buttonHandleChange(1000)}>
                +1000
              </Button>
            </Grid>
            <Grid item xs={4} sm={2}>
              <Button variant="contained" color="primary" onClick={(e) => buttonHandleChange(2000)}>
                +2000
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};


function UsersList({betrequest}) {
  const [userslist_data, setuserlist_data] = useState([])
  useEffect(()=>{
    setuserlist_data(get100users())
  }, [betrequest])
  
  return (
  <div>
  <h4>All The Participents</h4>
  <ListComponent data={userslist_data} />
  </div>
  );
  }



const MyPage = () => {
  const [betXdata, setBetXData] = useState(0);
  const [videoseek, setVideoSeek] = useState(0);
  const [amount, setAmount] = useState(0);
  const isAmount = useRef(false);
  const [makebet, setMakeBet] = useState(false);
  const [betrequest, setbetrequest] = useState(false);
  const [balance, setBalance] = useState(0);
  const [open, setOpen] = useState(false);
  const theme = useTheme();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  const handleBet = (e) => {
    e.preventDefault();
    setMakeBet(true);
  };

  const addMoney = (val) => {
    if (makebet) {
      return;
    }
    setAmount(prev => prev + val);
    if (amount < 0) {
      setAmount(0);
    }else{
      isAmount.current = true
    }
  };

  const resetAllState = () => {
    setBetXData(0);
    setAmount(0);
    setMakeBet(false);
    setOpen(false);
    setbetrequest(false)
  };

  const getBalanceData = () => {
    getBalance().then((data) => {
      if (!data) {
        return;
      }
      try{
        setBalance(data.data.balance);
    
      }catch(e){
        console.log(e)
      }
    });
  };

 

  useEffect(() => {
    getBalanceData();

    let count = 0;
    let t = generateRandomNumberWithProbability()
    const interval = setInterval(() => {
      // console.log(parseInt(t))
      if (count === parseInt(t)) {
        setVideoSeek(38);
        setOpen(true)
        setbetrequest(true)
      }
      if (count === 10){
        setMakeBet(true)
      }
      if (count > 2 && count < parseInt(t)){
        if (count < 10 || isAmount.current){
          setBetXData(prev => prev + Math.random() / 10);
        }else{
          setBetXData(prev => prev + Math.random());
        }
        
      }
      if (count === parseInt(t) + 10){
        // console.log(makebet, amount, balance)
        setBetXData(0)
        setVideoSeek(true)
        resetAllState();
        isAmount.current = false
        t = generateRandomNumberWithProbability()
        count = 0
      }
      count++;
      
    }, 1000);

    return () => clearInterval(interval); // Cleanup interval
  }, []);

  return (
    <div style={{ position: 'relative' }}>
      {open && <MyAlert message={`${betXdata.toFixed(2)}X  Money`} severity="success" handleClose={handleClose} open={open} />}
      <Grid container spacing={2} justifyContent="center" alignItems="center" style={{ minHeight: '130vh', padding: theme.spacing(2) }}>
        <Grid item xs={12} md={8} lg={9}>
          <Paper style={{ padding: 20, minHeight: '400px' }}>
            <VideoPlayer videoUrl="/videos/finalv2.mp4" jumpCondition={videoseek} displayText={betXdata} isMutedProp={true}/>
            <AddMoney addMoney={addMoney} amount={amount} handlebet={handleBet} makebet={makebet} setbalance = {setBalance} balance={balance} betrequest={betrequest} betXdata={betXdata}/>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4} lg={3}>
          <Paper style={{ padding: 20 }}>
            <UsersList betrequest={betrequest}/>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};

export default MyPage;
