import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Container,
} from '@mui/material';
import Avatar from '@mui/material/Avatar';
import KeyIcon from '@mui/icons-material/Key';
import { Link, useNavigate } from 'react-router-dom';
import {reset_password} from '../apis/user';
import LoaderOverlay from './loading';

export default function FormData() {
  const [isLoading, setLoading] = useState(false);
  let navigate = useNavigate()

  const [values, setValues] = useState({
    new_password: '',
    old_password:'',
    passwordHelper:'',
    confirm_password:"",
  });

  const handleChange = (name) => (event) => {
    setValues({ ...values, passwordHelper:'',[name]: event.target.value });
  };

  const passwordHandle = (e) => {
    e.preventDefault();

    const {
      old_password,
      new_password, confirm_password} = values;
    
    if (confirm_password != new_password){
      return setValues({...values, passwordHelper: "Password Not match"})
    }  

    setLoading(true);
    reset_password({
      old_password,
      new_password}).then((data) => {
      setLoading(false);
      if (!data) {
      
        return ;
      }
      if (data.error) {
        const a = data.error;
        return setValues({...values, passwordHelper: a})
        }
      if (data.status === 200){
        return navigate('/profile', {replace : true})
     
      }
      if (data.status === 400){
        return setValues({...values, passwordHelper: data.message})
      } 
       console.log(data)
    });
  };

  


  return (
    <Container maxWidth="sm" sx={{ minHeight: '800px' }}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          padding: '50px',
          borderRadius: '20px',
          boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
          marginTop: '100px',
        }}
      >
         <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <KeyIcon />
          </Avatar>
        <Typography variant="h5" component="h1" gutterBottom margin={2}>
          Password Change
        </Typography>
        {isLoading && <LoaderOverlay />}

        <Box component="form" sx={{ width: '100%' }}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                id="old_password"
                name="old_password"
                label="Old Password"
                variant="outlined"
                fullWidth
                onChange={handleChange('old_password')}
                sx={{ mb: 2 }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="password"
                name="password"
                label="Password"
                variant="outlined"
                type="password"
                fullWidth
                helperText = {values.passwordHelper}
                error = {Boolean(values.passwordHelper)}
                onChange={handleChange('new_password')}
                sx={{ mb: 3 }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="conf_password"
                name="conf_password"
                label="Re-Enter Password"
                variant="outlined"
                type="password"
                fullWidth
                helperText = {values.passwordHelper}
                error = {Boolean(values.passwordHelper)}
                onChange={handleChange('confirm_password')}
                sx={{ mb: 3 }}
              />
            </Grid>
            <Grid container justifyContent="center" spacing={3} margin={1}>
              
              <Grid item>
                <Button variant="contained" color="primary" onClick={passwordHandle}>
                  Update Password
                </Button>
                
              </Grid>
              <Grid
                container
                direction="column"
                justifyContent="center"
                alignItems="center"
                 margin={1}>
                  <Grid justifyContent='center' item margin={1}>
                  <Link to='/profile' variant="body2">
                  {"Profile"}
                </Link> 
                  </Grid>

                 </Grid>
            </Grid>
          </Grid>
        </Box>
       

      </Box>
    </Container>
  );
}
